import NodeUtil from "../../../Tools/NodeUtil";
import Util from "../../../Tools/Util";

export default class BanKItem {
    private node: cc.Node;
    private data: any;
    private button: cc.Button;
    private bankSprite: cc.Sprite;
    public Init(node: cc.Node, data: any, onclickCallback: (bankType: number) => void) {
        this.node = node;
        this.node.active = true;
        this.data = data;
        this.button = this.node.getComponent(cc.Button);
        this.bankSprite = this.node.getChildByName("bankSprite").getComponent(cc.Sprite);
        let spirteName = ""
        this.SetTextSprite(false, spirteName)
        Util.SetClickAction(this.button, () => {
            this.SetTextSprite(true, spirteName)
            onclickCallback(this.data);
        });
    }
    private SetTextSprite(select: boolean, spirteName: string) {
        // if (select)
        //     NodeUtil.SetSpriteFrame(this.bankSprite, "MainGame/Atlas/pay", spirteName);
        // else
        //     NodeUtil.SetSpriteFrame(this.bankSprite, "MainGame/Atlas/pay", spirteName);
    }
}